const NavButton = (text, onClick, na=false) => {
    return (
        <div className={"Navbar-button"+(na?" notallowed":"")} onClick={()=>onClick(text)} >{text}</div>
    )
}

export default ({onClick}) => {
    return (
        <div className="Navbar">
            <div className="left Menu">
                {NavButton('people',onClick)}
                {NavButton('things',onClick)}
                {NavButton('kids',onClick)}
            </div>
            <div className="Navbar-middle notallowed">MB PHOTOGRAPHY</div>
            <div className="right Menu">
                {NavButton('gallery','art', true)}
                {NavButton('social',onClick, true)}
                {NavButton('contact',onClick, true)}
            </div>
        </div>
    )
}